import S from 'fluent-json-schema'

export type iBigInt = string

/**
 * Block entity
 */
export interface Block {
  number: number
  timestamp: number
}

export type BlockNumber = bigint | number | string

/**
 * Deep partial definition
 */
export type DeepPartial<T> = {
  [P in keyof T]?: DeepPartial<T[P]>
}

/**
 * Locales management
 */
export type Locales<T = string> = { [locale: string]: T }

/**
 * Meta management
 */
export type MetaContent = { [key: string]: Locales<string> }

export interface MetaItem {
  title: Locales<string>
  description?: Locales<string>
}

export type MetaItems = { [key: string]: MetaItem[] }

/**
 * Reward
 */
export interface Reward {
  // The token IDentifier
  tokenId: string
  // Total amount of accrued|received tokens
  amount: iBigInt
  // Amount converted in USD
  amountUSD: iBigInt
  // APY generated from|by this reward (so far | from the beginning)
  APR: number
  // Percentage
  percentage: number
}

export type UnitTime =
  | 'seconds'
  | 'minutes'
  | 'hours'
  | 'days'
  | 'months'
  | 'years'

export function sUnitTime() {
  return S.string().enum([
    'seconds',
    'minutes',
    'hours',
    'days',
    'months',
    'years',
  ])
}

export interface Period {
  duration: number
  unit: UnitTime
}
